import React from 'react'
import { Link } from 'gatsby'
import { ModalRoutingContext } from 'gatsby-plugin-modal-routing'
import "../styles/styles.css"
import image1 from "../../static/PhobiaVR/image1.png"

const ModalExamplePage = () => (
  <ModalRoutingContext.Consumer>
    {({ modal, closeTo }) => (
      <div>
        {modal ? (
          <Link to={closeTo} class="close">
            Close
          </Link>
        ) : (
          <header>
            <h1>
              Website Title
            </h1>
          </header>
        )}

        <h1>Exposure Therapy VR</h1>
        
        <img class="phobia" src={image1} alt="image1"/>

        <div>
          <p>
          This app is part of an ongoing project for developing a VR for treating different kinds of phobias. In this case the fear of heights. The app is made in Unity. It is meant to be played with a supervisor and with sensors attached to the user to create a safe environment. 
          I have presented it as my Master's thesis.
          The project will be continued by the Polytechnic University of Bucharest. 
          </p> 
        </div>
      </div>
    )}
  </ModalRoutingContext.Consumer>
)

export default ModalExamplePage